body.page-events.page-details {
  $container-width: $desktop-medium - 30px;

  #zone2 {
    width: 100%;
  }

  #zone3,
  .catal-nav-ex {
    display: none;
  }

  .event-view-full {
    max-width: 823px;
    margin: 0 auto;
  }

  .list-articles { // header image
    position: relative;
    z-index: 1;
    margin: 25px 0 0;
    text-align: center;

    + .catal-ex-details {
      margin-top: -30px;
    }

    img {
      display: block;
      margin: 0 auto;
    }
  }

  .event-view-full {
    .catal-ex-item-buttons { // floating buttons
      position: absolute !important;
      left: 15px;
      margin-top: 200px;
      padding-right: 15px;
      max-width: percentage(($container-width - 823px) / $container-width) / 2;

      .btn-primary {
        @extend %button;
        display: flex;
        width: 100%;
        margin-left: 0;
        margin-bottom: 15px;
        padding-left: 16px;
        padding-right: 16px;
        align-items: center;
        justify-content: center;

        &[data-action='add'] > .selection-empty,
        &[data-action='remove'] > .selection-full {
          display: flex;
          align-items: center;
        }

        .icon-catal {
          order: 99;
          margin-left: 10px;
          margin-right: 0;
          font-size: 12px;
          color: $theme-color-2;
          transform: scale(1);
        }
      }
    }

    .catal-ex-item-stand-more-link {
      font-size: 14px;
      color: $theme-color-2;

      .icon-catal {
        display: none;
      }
    }

    .catal-ex-details {
      position: relative;
      z-index: 0;
      padding: 30px 0;

      &::after { // overflow background
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        width: 100vw;
        max-width: $container-width;
        height: 100%;
        background: $lightgrey-3;
        transform: translateX(-50%);
      }

      .edito {
        margin: 0;
      }
    }


    .catal-ed-group {
      border: 0;
    }

    .catal-ed-group-title {
      padding: 0;
      border: 0;
    }

    .catal-ed-activity {
      .catal-ed-activity-group {
        padding: 0;
        border: 0;
      }

      .catal-ed-activity-list {
        line-height: 22px;
      }

      .catal-ed-activity-item {
        padding: 0;
      }
    }

    .catal-ed-activity-title {
      line-height: 22px;
    }

    .catal-ed-activity-link {
      @extend %button-underline;
      line-height: 22px;
    }
  }

  .catal-ed-main {
    margin: 30px 0;
    padding: 0;
    background: none;
    border: 0;
  }

  .catal-ed-main-title,
  .catal-ed-main-desc-title {
    @extend %content-h2Like;
    text-align: left;
  }

  .catal-ed-main-desc {
    margin-top: 30px;
  }

  .catal-ed-main-session {
    width: 50%;
  }

  .catal-ed-main-address {
    margin-top: 0;

    .icon-catal {
      margin-right: 15px;
      font-size: 22px;
      line-height: 22px;
      vertical-align: text-bottom;
      color: $theme-color-2;
    }

    span {
      position: relative;
      z-index: 0;
      display: inline-block;
      padding: 0 10px;
      font-family: $font-1;
      font-size: 14px;
      font-weight: 500;
      font-style: normal;
      line-height: 22px;
      letter-spacing: 0;
      color: #fff;

      &::after { // background
        content: '';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        width: calc(100% + 14px);
        height: 100%;
        background: $textcolor-1;
        transform: translate(-50%, -50%) skewX(-25deg);
      }
    }
  }

  .event-link-opposite {
    padding: 0;
    font-weight: 700;
    color: $theme-color-2;
    border: 0;
  }

  /// category and thema tags
  .evd-infos-type-container {
    margin: 15px 0;
    display: flex;
    flex-direction: row-reverse;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  .evd-infos-type {
    display: inline-block;
    text-align: center;

    .evd-infos-type-title {
      @include visually-hidden;
    }

    .evd-infos-type-list {
      margin: 0;
      padding: 0;
      line-height: 1;
    }

    .event-type-li {
      display: inline-block;
      margin: 0 15px;

      &::before {
        display: none;
      }
    }

    .event-link {
      position: relative;
      z-index: 0;
      display: inline-block;
      padding: 0 10px;
      font-family: $font-1;
      font-size: 16px;
      font-weight: 500;
      font-style: normal;
      line-height: 23px;
      letter-spacing: 0;
      color: $textcolor-2;

      &::after { // background
        content: '';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        width: calc(100% + 14px);
        height: 100%;
        background: $theme-color-1;
        transform: translate(-50%, -50%) skewX(-25deg);
      }
    }

    &:nth-child(2) {
      .event-link {
        color: #fff;

        &::after {
          background-color: $textcolor-1;
        }
      }
    }
  }
}
