@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.social-sharing {
  body.page-events &,
  body.article-program &,
  body.sial-innovation & {
    order: 2;
    margin: 30px 0;
    padding: 25px 26px;
    text-align: center;
    flex-grow: 0;
    flex-shrink: 0;
    background: $lightgrey-3;
    border: 0;

    .sr-title {
      margin: 5px 0 13px;
      font-family: $font-2;
      font-size: 12px;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: 2.24px;
      text-transform: uppercase;
      color: $lightgrey-2;
    }

    .sr-list {
      display: flex;
      margin: 0;
      padding: 0;
      align-items: flex-start;
      justify-content: center;
    }

    .sr-item {
      position: relative;
      width: 45px;
      height: 45px;
      margin: 0;
      padding: 0;
      border: 0;

      a {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        width: 100%;
        height: 100%;
        padding: 0;
        align-items: center;
        justify-content: center;
        border: 0;
        user-select: none;

        .icon,
        .sr-item-txt {
          display: none;
        }

        &::before {
          @include icomoon;
          font-size: 45px;
          color: $lightgrey-1;
          transition: color 0.2s ease-in-out;
        }

        &.sr-link-mail {
          &::before {
            content: map_get($icon-list, 'mail-open');
          }
        }

        &.sr-link-facebook {
          &::before {
            content: map_get($icon-list, 'facebook');
          }
        }

        &.sr-link-twitter {
          &::before {
            content: map_get($icon-list, 'twitter');
          }
        }

        &.sr-link-linkedin {
          &::before {
            content: map_get($icon-list, 'linkedin');
          }
        }

        &:hover,
        &:active {
          &::before {
            color: $theme-color-2;
          }
        }
      }

      &:not(:first-of-type) {
        margin-left: 12px;
      }
    }
  }
}

.article-title {
  @extend .edito-custom;

  body.sial-innovation & {
    margin: 0 0 40px;

    @include mq_min_width($tablet-medium) {
      margin: 0 0 80px;
    }

    @include mq_min_width($desktop-medium) {
      margin: -45px 0 80px;
    }

    & > .inside {
      @include mq_min_width($desktop-small) {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
      }

      .at-content {
        margin: 0;

        @include mq_min_width($desktop-small) {
          order: 1;
          width: calc(51.2% - 30px);
        }

        @include mq_min_width($desktop-medium) {
          padding-top: 56px;
        }

        .article-intro {
          width: 100%;
          margin: 0;
          font-size: 20px;

          @include mq_min_width($desktop-medium) {
            width: calc(100% - 77px);
          }

          ul,
          ol {
            padding-left: 20px;
          }
        }

        .blocs-starts {
          width: 100%;
          margin-top: 27px;

          @include mq_min_width($desktop-medium) {
            width: calc(100% + 77px);
          }

          @include mq_min_width($tablet-medium) {
            display: flex;
            align-items: stretch;
            justify-content: space-between;
          }

          .social-sharing {
            width: 100%;

            @include mq_min_width($tablet-medium) {
              width: calc(50% - 15px);
            }

            @include mq_min_width($desktop-small) {
              padding: 25px 15px;
            }

            @include mq_min_width($desktop-medium) {
              padding: 25px 26px;
            }

            & + .award-ceremony {
              margin-top: 30px;
              @include mq_min_width($tablet-medium) {
                margin-top: 0;
              }
            }
          }

          .award-ceremony {
            order: 1;
            position: relative;
            display: flex;
            width: 100%;
            margin-right: auto;
            margin-left: auto;
            padding: 25px 26px;
            font-family: $font-2;
            font-size: 20px;
            font-weight: 900;
            line-height: 30px;
            text-align: center;
            align-items: center;
            justify-content: center;
            flex-grow: 0;
            flex-shrink: 0;
            background: $theme-color-1;
            overflow: hidden;

            @if $is_mobile {
              max-width: 320px;
            }

            @include mq_min_width($tablet-small) {
              width: calc(50% - 15px);
              min-width: 290px;
            }

            @include mq_min_width($tablet-medium) {
              min-width: 0;
              margin-right: 0;
              margin-left: 0;
            }

            &::before {
              @include icomoon;
              content: map_get($icon-list, 'pedestal');
              position: absolute;
              bottom: 0;
              left: 0;
              font-size: 115px;
              color: $theme-color-1-dark;
            }
          }

          .award-ceremony__title {
            position: relative;
            z-index: 1;
          }
        }

        .at-main-title {
          width: 100%;
          margin-left: 0;
          border-bottom: 1px solid $lightgrey-1;

          @include mq_min_width($desktop-medium) {
            width: calc(100% - 77px);
          }

          a {
            display: block;
          }

          .hallb {
            position: relative;
            top: -7px;
            display: inline-block;
            margin-right: 100%;
            padding-top: 1px;
            padding-left: 39px;
            font-family: $font-3;
            font-size: 12px;
            font-weight: 300;
            letter-spacing: 0.89px;
            white-space: nowrap;
            color: #fff;

            @include mq_min_width($tablet-medium) {
              top: -12px;
            }

            &::before,
            &::after {
              position: absolute;
              top: 50%;
            }

            &::after {
              content: '';
              z-index: -1;
              left: 29px;
              width: calc(100% - 20px);
              height: 23px;
              background: $textcolor-2;
              transform: translateY(-50%) skewX(-25deg);
            }

            &::before {
              @include icomoon;
              content: map_get($icon-list, 'marker');
              left: 0;
              font-size: 23px;
              letter-spacing: normal;
              color: $theme-color-2;
              transform: translateY(-50%);
            }
          }
        }
      }
    }
  }
}

.article-wrapper {
  .article-intro {
    body.no-accroche & {
      display: none;
    }
  }

  .article-content {
    body.no-accroche & {
      max-width: none;
    }

    > p {
      margin-bottom: 28px;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .social-sharing {
    display: none;
  }

  body.article & {
    padding-top: 30px;

    .article-title {
      margin: 0;

      .inside {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        text-align: center;
      }

      .at-theme {
        float: none;
        margin: 0;
        padding: 0;
        font-family: $font-2;
        font-size: 12px;
        font-weight: 500;
        letter-spacing: 1.04px;
        color: $textcolor-2;
        background: none;

        & + .at-main-title {
          margin-top: 15px;
        }

        @each $topic-name, $topic-color in $topics-color {
          $foreground-color: map_get($topic-color, 'foreground');
          $background-color: map_get($topic-color, 'background');

          &.topic_#{$topic-name} {
            position: relative;
            z-index: 0;
            color: if($foreground-color, $foreground-color, currentColor);

            &::after { // background
              content: '';
              position: absolute;
              z-index: -1;
              top: 50%;
              left: 50%;
              width: calc(100% + 14px);
              height: 100%;
              background: $background-color;
              transform: translate(-50%, -50%) skewX(-25deg);
            }
          }
        }
      }

      .at-main-title {
        margin: 0 0 28px;
      }

      .at-content {
        order: 1;
        margin: 0;
      }

      img.at-illust,
      img.gla-item-img,
      img.la-item-img,
      img.cx-lazy-load {
        order: 2;
        float: none;
        display: block;
        width: auto;
        margin: 0;
      }
    }

    .article-content {
      @include clearfix;
      margin-top: 51px;

      &::before {
        body.no-accroche & {
          content: '';
          display: block;
          height: 1px;
        }
      }

      figure {
        display: inherit;

        //&.fl {
        //  margin-right: 0;
        //  margin-left: 0;
        //
        //  @include mq_min_width($tablet-medium) {
        //    float: left;
        //    margin-right: 20px;
        //    padding: 5px;
        //    text-align: left;
        //    border: 1px solid #e3e9ea;
        //  }
        //}

        //&.fr {
        //  margin-right: 0;
        //  margin-left: 0;
        //
        //  @include mq_min_width($tablet-medium) {
        //    float: right;
        //    margin-left: 20px;
        //    padding: 5px;
        //    text-align: left;
        //    border: 1px solid #e3e9ea;
        //  }
        //}
      }
    }
  }

  body.article-program & {
    position: relative;

    .article-title {
      .topic_hall_b {
        order: 2;
        position: relative;
        padding: 0 20px;
        font-family: $font-3;
        font-size: 12px;
        font-weight: 600;
        line-height: 2.16;
        letter-spacing: 0.89px;
        color: #FFF;

        &::before {
          content: '';
          position: absolute;
          z-index: -1;
          top: 10px;
          left: calc(50% - 30px);
          height: 23px;
          width: 60px;
          background: $textcolor-2;
          transform: translateY(-50%) skewX(-25deg);
        }
      }

      .at-content {
        display: flex;
        flex-direction: column;
      }

      .at-main-title {
        order: 1;
        margin: 0 0 24px;
        font-family: $font-2;
        font-size: 41px;
        font-weight: 900;
        line-height: 63px;
        text-align: center;
        color: $textcolor-3;
      }

      p:last-of-type {
        order: 3;
        margin-bottom: 24px;
        font-size: 20px;
        font-weight: 600;
        line-height: 1.55;
        text-align: center;
        color: $lightgrey-2;
      }

      img.at-illust,
      img.gla-item-img,
      img.la-item-img,
      img.cx-lazy-load {
        width: calc(100% - 8.3334%);
        max-width: 823px;

        @include mq_min_width($tablet-medium) {
          width: calc(100% - 33.3334%);
        }
      }
    }

    .article-intro {
      display: block;

      > p {
        display: none;
      }
    }

    .article-content {
      position: relative;
      margin: 0 -8.3334% 80px;
      font-size: 16px;
      font-weight: 300;
      line-height: 1.56;

      @include mq_min_width($tablet-medium) {
        margin-right: 0;
        margin-left: 0;
      }

      .lien2 {
        color: $theme-color-2;
      }

      .lien1 {
        @extend %button;
      }

      p,
      ul {
        padding: 10px 8.3334%;

        @include mq_min_width($tablet-medium) {
          padding: 10px 16.6667%;
        }
      }

      p:nth-of-type(-n+3),
      p:nth-of-type(n+6),
      ul:first-of-type {
        margin: 0;
        background-color: $lightgrey-3;
      }

      p {
        &:first-of-type {
          margin-top: 40px;
        }

        &:nth-of-type(3) {
          padding-bottom: 40px;
        }

        &:nth-of-type(4) {
          margin: 40px 0 0;
        }

        &:nth-of-type(5) {
          margin-top: 0;
        }

        &:nth-of-type(6) {
          padding-top: 40px;

          @include mq_min_width($tablet-medium) {
            padding-right: 8.3334%;
            padding-left: 30%;
          }

          @include mq_min_width(900px) {
            padding-left: 26%;
          }

          @include mq_min_width($desktop-medium) {
            padding-top: 40px !important;
            padding-left: 22%;
          }
        }

        &:nth-of-type(7) {
          a::before {
            content: '\A';
            white-space: pre;
          }
        }

        &:nth-of-type(n+6) {
          font-size: 14px;

          @include mq_min_width($tablet-medium) {
            padding: 10px 8.3334% 10px 30%;
            font-size: 20px;
          }

          @include mq_min_width(900px) {
            padding-left: 26%;
          }

          @include mq_min_width($desktop-medium) {
            padding-left: 22%;
          }
        }

        &:last-of-type {
          padding-bottom: 20px;
          font-weight: 700;

          @include mq_min_width($tablet-medium) {
            padding-bottom: 55px;
          }
        }

        a {
          text-decoration: none;
        }
      }

      &::before {
        position: absolute;
        z-index: -1;
        top: -150px;
        width: 100%;
        height: 150px;
        background-color: $lightgrey-3;

        @include mq_min_width($desktop-small) {
          top: -100px;
          height: 100px;
        }
      }

      ul {
        line-height: 2.312;

        li {
          margin-left: 30px;

          &::before {
            background: #000;
            border-radius: 50%;
          }
        }
      }

      iframe {
        display: block;
        max-width: 90%;
        margin: 30px auto 80px;

        @include mq_min_width($desktop-small) {
          max-width: 66.6667%;
        }
      }

      figure.fl {
        position: relative;
        margin: 0 auto;
        padding: 30px 20px 0;
        text-align: center;
        background-color: $lightgrey-3;
        border: 0;

        &::before {
          content: '';
          position: absolute;
          z-index: -1;
          top: 0;
          right: 0;
          left: 50%;
          display: block;
          width: 100vw;
          height: 100%;
          background-color: $lightgrey-3;
          transform: translateX(-50%);
        }

        @include mq_min_width($tablet-medium) {
          margin: 0;

          &::before {
            content: none;
          }
        }

        @include mq_min_width($desktop-medium) {
          padding: 50px;
        }
      }
    }

    .social-sharing {
      display: flex;
      transform: translateY(20px);
      padding: 5px;
      margin-top: 0;
      margin-bottom: 50px;

      @include mq_min_width($desktop-medium) {
        position: absolute;
        left: 0;
        top: 88px !important;
        display: block;
        border: 0;
        transform: none;
        background: none;
        width: 210px;
      }

      .inside {
        margin: auto;

        @include mq_min_width($desktop-medium) {
          margin: 0;
        }
      }

      .sr-list {
        justify-content: space-evenly;
      }

      .sr-title {
        font-size: 11px;
      }

      .sr-item {
        width: 24px;
        height: 24px;

        @include mq_min_width($desktop-medium) {
          width: 37px;
          height: 37px;
        }

        a::before {
          font-size: 37px;
        }
      }
    }

    .sr-item-txt {
      @include visually-hidden;
    }
  }
}
