.article-navigation {
  body.article & {
    max-width: 823px;
    margin: 30px auto;
  }

  body.article-program & {
    position: relative;
    max-width: none;
    margin: 80px -80px;
    padding: 80px 60px;
    background-color: $lightgrey-3;
  }

  &::before { // overflow background
    body.article-program & {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      left: 50%;
      display: block;
      width: 100vw;
      height: 100%;
      background-color: $lightgrey-3;
      transform: translateX(-50%);
    }

    @include mq_min_width($tablet-medium) {
      margin-right: auto;
      margin-left: auto;
      padding: 80px 0;
    }
  }

  .an-list {
    @include clearfix;

    body.article-program & {
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      flex-direction: column;

      @include mq_min_width($tablet-medium) {
        flex-direction: row;
      }
    }
  }

  .an-item-content {
    body.article-program & {
      position: absolute;
      z-index: 2;
      bottom: 0;
      left: 0;
      display: flex;
      width: 100%;
      padding: 20px 30px;
      flex-direction: column;
      color: #fff;
    }
  }

  .an-item-title {
    body.article-program & {
      margin-top: 22px;
      margin-bottom: 10px;
      font-family: $font-2;
      font-size: 24px;
      font-weight: 900;
    }
  }

  .an-item-link {
    body.article-program & {
      @include clearfix;
      display: flex;
      width: 100%;
      height: 100%;
      min-height: 200px;
      align-items: stretch;
      border-radius: 6px;
      overflow: hidden;

      @include mq_min_width($tablet-medium) {
        min-height: 437px;
      }
    }
  }

  .an-item {
    body.article-program & {
      width: 98%;
      max-width: 500px;
      height: 100%;
      margin: 15px auto;
      border-radius: 6px;
      box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);

      @include mq_min_width($tablet-medium) {
        width: 48%;
      }

      @include mq_min_width($desktop-small) {
        &:nth-child(odd) {
          margin-right: 15px;
          margin-left: auto;
        }

        &:nth-child(even) {
          margin-right: auto;
          margin-left: 15px;
        }
      }
    }

    img {
      body.article-program & {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
      }
    }

    .icon {
      display: none;
    }
  }

  .an-item-publication {
    display: none;
  }
}
