body.detail-actu {
  .article-wrapper {
    position: relative;

    .article-title {
      > .inside {
        .at-content .at-main-title {
          @extend .main-title;
          border: 0; 
          margin-top: 20px;
          margin-bottom: 101px;

          @include mq_min_width($desktop-medium) {
            font-size: 42px;
            line-height: 1.5;
          }
        }
        
        img.cx-lazy-load,
        img.at-illust {
          width: 100%;
          max-width: 823px;
          margin-top: 35px;

          @include mq_min_width(520px) {          
            margin-top: 0;
          }

          @include mq_min_width($desktop-small) {          
            height: 382px;
          }
        }
      }
      .at-content {
        width: 100%;
      }
    }

    .article-intro {
      position: absolute;
      top: 130px;
      left: 0;
      right: 0;
      display: block;
      width: 100%;

      @include mq_min_width(520px) {
        top: 110px;
      }
      
      @include mq_min_width($desktop-medium) {
        top: 160px;
      }

      p {
        color: $lightgrey-2;
        margin: auto;
        font-size: 20px;
        font-weight: 600;
        line-height: 1.55;
        text-align: center;

        @include mq_min_width($desktop-small) {
          width: 50%;
        }
      }
    }

    .article-content {
      position: relative;
      margin-bottom: 80px;
      margin-top: -40px;
      padding: 105px 15px 80px;

      @include mq_min_width($tablet-medium) {
        margin-top: -150px;
        padding-top: 225px;
      }

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 50%;
        width: 100vw;
        height: 100%;
        background-color: $lightgrey-3;
        z-index: -1;
        transform: translateX(-50%);
        
        @include mq_min_width($desktop-medium) {
          transform: initial;
          left: -246px;
        }
      }

      h2, 
      h3, 
      h4 {
        @extend .main-title;
      }

      h2 {
        margin-bottom: 16px;

        @include mq_min_width($tablet-medium) {
          font-size: 40px;
          line-height: 1.5;
        }
      }

      h3,
      h4 {
        color: $textcolor-2;
      }

      h3 {
        font-size: 25px;
        line-height: 1.6;
        margin-top: 15px;
        margin-bottom: 20px;
      }

      h4 {
        font-size: 20px;
        line-height: 2;
        margin-top: 15px;
        margin-bottom: 15px;
      }

      > * {
        margin: auto;

        @include mq_min_width($desktop-small) {
          width: 66.6667%;
          min-width: 823px;
        }
      }

      p {
        margin-top: 15px;
        margin-bottom: 15px;
      }

      ul {
        li {
          &::before {
            border-radius: 50%;
            background: $textcolor-2;
          }
        }
      }

      blockquote {
        @extend .blockquote;
        margin: 36px auto 41px;

        @include mq_min_width($desktop-small) {
          padding: 68px 56px 69px 88px;
        }

        p {
          color: $textcolor-2;
          font-family: $font-2;
          font-size: 25px;
          font-weight: 500;
          line-height: 1.68;
          border: 0;
          position: relative;
          z-index: 1; 
        }

        i {
          display: none;
        }
      }

      iframe {
        display: block;
        max-width: 100%;
        margin-top: 40px;
        margin-bottom: 46px;
      }
    }

    .social-sharing {
      display: flex;
      margin: 20px auto 50px;

      > .inside {
        margin: auto;
      }
      .social-sharing {
        margin: 0 auto;
        padding: 0;
      }

      @include mq_min_width($desktop-medium) {
        transform: none;
        display: block;
        position: absolute;
        left: -5px;
        top: 122px;
        border: 0;
        margin-top: 0;
        background: none;
        padding: 0;
      }
    }
  }
}